// Container which hold youtube player and controlls of that player.
// It has its own controller, to changing flag when api ready
// and to declare functionallity that we will use in youtubeplayer directive

(function() {

  'use strict';

  angular
    .module('musicHead')
    .directive('youtubePlayerContainer', youtubePlayerContainer);

  youtubePlayerContainer.$inject = ['ytPlayerApi','youtubeFactory', 'dataService', '$routeParams'];

  function youtubePlayerContainer(ytPlayerApi) {

    return {
      restrict: "E",
      templateUrl: 'app/components/YouTube/PlayerContainer/playerContainer.html',
      controller: ytContainerController,
      controllerAs: 'ytContainerCtrl'
    }

    function ytContainerController($scope, youtubeFactory, dataService, $routeParams) {
      var vm = this,
          routeArtist = $routeParams.artist;

      vm.apiReady = false;
      vm.currentDuration;
      vm.currentPage = 1;
      vm.currentTime;
      vm.duration;
      vm.filterThumbnails = filterThumbnails();
      vm.getProgressValue = getProgressValue;
      vm.link = link;
      vm.pageSize = 4;
      vm.progress = 0;
      vm.volume = 100;

// Changing the flag, that informs youtube api is ready. in this way, api will be ready
// when this directive is loaded, and will be ready even on another route.

      ytPlayerApi.onReady()
        .then(function() {
            vm.apiReady = true;
      })

// Sending controlls to youutubeplayer directive for events

      function getProgressValue() {
        $scope.$broadcast('progress', vm.progress);
      }

      $scope.$on('currentVideoDuration', function() {
        vm.currentDuration = arguments[1];
      });

      function link(id) {
        return dataService.getTrustedThumbnailSrc(id);
      }

      function filterThumbnails() {
        return {snippet: {title: routeArtist || ''}};
      }
    }
  }
})();

(function() {

  'use strict';

  angular
    .module('musicHead')
    .directive('youtubePlayer', youtubePlayer);

  youtubePlayer.$inject = ['$window','YT_event','$interval','youtubeFactory','helpersFactory','$routeParams'];

  function youtubePlayer($window, YT_event, $interval, youtubeFactory, helpersFactory, $routeParams) {

    var myPlayer;

    return {
      restrict: "E",
      scope: {
        videoid: "="
      },
      require: '^youtubePlayerContainer',
      template: '<div></div>',
      link: function(scope, element, attributes, youtubePlayerContainerCtrl) {
        var dbTitle,
            myClips = youtubeFactory.readCache(),
            routeArtist = $routeParams.artist;

  // Condition that checks if we are on a specific artist route

        (myClips.length !== 0 && angular.isDefined(routeArtist)) ?  myClips = getRouteArtists() : void(0);

  // Create Player

        myPlayer =  new YT.Player(element.children()[0], {
          videoId: scope.videoid,
          playerVars: {
            html5: 1,
            modesbranding: 1,
            iv_load_policy: 3,
            showinfo: 1,
            controls: 0,
            autoplay: 1,
            fs: 1
          },
          events: {
            'onReady': initialize,
            'onStateChange': changeVideo
          }
        });

    // Function called by 'onReady' youtube player event

        function initialize() {
          updateTimerDisplay();
          updateProgressBar();
          scope.$emit('currentVideoDuration', myPlayer.getDuration());
          // Clear any old interval.
          $interval.cancel(time_update_interval);
          // Start interval to update elapsed time display and
          // the elapsed part of the progress bar every second.
          var time_update_interval = $interval(function() {
            updateTimerDisplay();
            updateProgressBar();
          }, 1000)
        }

      // This function is called by initialize()

        function updateTimerDisplay() {
          if(myPlayer.getCurrentTime) {
            youtubePlayerContainerCtrl.currentTime = helpersFactory.formatTime(myPlayer.getCurrentTime());
            youtubePlayerContainerCtrl.duration = helpersFactory.formatTime(myPlayer.getDuration());
          }
        }

        function updateProgressBar() {
          if(myPlayer.getCurrentTime) {
            youtubePlayerContainerCtrl.progress = ((myPlayer.getCurrentTime() / myPlayer.getDuration()) *100);
          }
        }

    // Function called by 'onStateChange' youtube player event

        function changeVideo(event) {
          if(event.data == YT.PlayerState.ENDED) {
            var nextVideoIndex;
            angular.forEach(myClips, function(item) {
              item.id.videoId === scope.videoid ? nextVideoIndex = myClips.indexOf(item) + 1 : false;
            });
            nextVideoIndex === myClips.length ? (nextVideoIndex = 0, youtubePlayerContainerCtrl.currentPage = 0) : false;
            myPlayer.cueVideoById(myClips[nextVideoIndex].id.videoId);
            myPlayer.playVideo();
            scope.videoid = myClips[nextVideoIndex].id.videoId;
            nextVideoIndex % youtubePlayerContainerCtrl.pageSize === 0 ? youtubePlayerContainerCtrl.currentPage ++ : false;
          }
        }

       // Function called by click in a previous button

        function playPrevious() {
          var currentPlayerState = myPlayer.getPlayerState(),
              previousVideoIndex;
          if(currentPlayerState == 1) {
            angular.forEach(myClips, function(item) {
              item.id.videoId === scope.videoid ? previousVideoIndex = myClips.indexOf(item) - 1 : false;
            });
            previousVideoIndex === -1 ? (previousVideoIndex = myClips.length - 1, youtubePlayerContainerCtrl.currentPage = myClips.length/youtubePlayerContainerCtrl.pageSize +1) : false;
            myPlayer.cueVideoById(myClips[previousVideoIndex].id.videoId);
            myPlayer.playVideo();
            scope.videoid = myClips[previousVideoIndex].id.videoId;
            previousVideoIndex % youtubePlayerContainerCtrl.pageSize === 3 ? youtubePlayerContainerCtrl.currentPage -- : false;
          }
        }

    // Function that overwrite vm.clips on specific artist route

        function getRouteArtists() {
          var myRouteClips = myClips.filter(function(clip) {
            dbTitle = clip.snippet.title.toLowerCase();
            routeArtist = routeArtist.toLowerCase();
            if(dbTitle.indexOf(routeArtist) > -1) {
              return true;
            }
          });
          return myRouteClips;
        }

    // Watching if videoId have changed, generally. If it has, placeing new Id in player.

        scope.$watch('videoid', function(newValue, oldValue) {
          if(newValue == oldValue) {
            return;
          }
          myPlayer.cueVideoById(scope.videoid);
          scope.$emit('newId', scope.videoid);
          myPlayer.playVideo();
        });

    // Handlers for player controlls.

        scope.$on(YT_event.STOP, function() {
          myPlayer.seekTo(0);
          myPlayer.stopVideo();
        });

        scope.$on(YT_event.PLAY, function() {
          myPlayer.playVideo();
        });

        scope.$on(YT_event.PAUSE, function() {
          myPlayer.pauseVideo();
        });

        scope.$on(YT_event.NEXT, function() {
          myPlayer.seekTo(myPlayer.getDuration());
        });

        scope.$on(YT_event.PREVIOUS, function() {
          myPlayer.seekTo(0);
          playPrevious();
        });

        scope.$on(YT_event.REPEAT, function() {
          myPlayer.seekTo(0);
        });

        scope.$on(YT_event.MUTE, function() {
          myPlayer.isMuted() ? myPlayer.unMute() : myPlayer.mute();
        });

        scope.$on('progress', function() {
          var newTime = myPlayer.getDuration() * (youtubePlayerContainerCtrl.progress / 100)
          myPlayer.seekTo(newTime);
        });

        scope.$on(YT_event.VOLUME, function(event, volume) {
          myPlayer.setVolume(volume);
        });
      }
    };
  }
})();

(function() {

  'use strict';

  angular
    .module('musicHead')
    .service('favouritesService', favouritesService);

  favouritesService.$inject = ['$window', '$location','$timeout','toastr'];

  function favouritesService($window, $location, $timeout, toastr) {

    var self = this;

    self.favourites = {
      playlists : []
    }

    self.activePlaylist = {
      activePlaylist: {
        name:'no playlist created'}
    }

    self.checkLocalStorage = checkLocalStorage;
    self.addToFavourites = addToFavourites;
    self.toggleActive = toggleActive;
    self.setActivePlaylist = setActivePlaylist;
    self.addNewPlaylist = addNewPlaylist;
    self.makeXshowPlaylist = makeXshowPlaylist;
    self.makeXhidePlaylist = makeXhidePlaylist;
    self.makeXshowArtist = makeXshowArtist;
    self.makeXhideArtist = makeXhideArtist;
    self.removePlaylist = removePlaylist;
    self.removeArtist = removeArtist;
    self.updateLocalStorageString = updateLocalStorageString;

    function checkLocalStorage() {
      if(localStorage.favourites) {
        self.favourites.playlists = angular.fromJson(localStorage.favourites).playlists;
      }
      if(localStorage.activePlaylist) {
        self.activePlaylist.activePlaylist = angular.fromJson(localStorage.activePlaylist);
      }
      updateLocalStorageString();
    }

    function addToFavourites(artist) {
      if(self.activePlaylist.activePlaylist.name === 'no playlist created') {
        toastr.info('You have to create and select playlist in order to add artist to it')
      } else {
        for (var i = 0; i < self.activePlaylist.activePlaylist.artists.length; i ++) {
          if (self.activePlaylist.activePlaylist.artists[i].name === artist) {
            return;
          }
        }
        self.activePlaylist.activePlaylist.artists.push({name:artist, active: true});
        localStorage.activePlaylist = angular.toJson(self.activePlaylist.activePlaylist);
        localStorage.favourites = angular.toJson(self.favourites);
        updateLocalStorageString();
      }
    }

    function toggleActive(artist) {
      for (var i = 0; i < self.activePlaylist.activePlaylist.artists.length; i ++) {
        if (self.activePlaylist.activePlaylist.artists[i].name === artist) {
          self.activePlaylist.activePlaylist.artists[i].active = !self.activePlaylist.activePlaylist.artists[i].active;
          localStorage.activePlaylist = angular.toJson(self.activePlaylist.activePlaylist);
          updateLocalStorageString();
          return;
        }
      }
    }

    function setActivePlaylist(playlist) {
      for(var i = 0; i < self.favourites.playlists.length ; i ++) {
        if(self.favourites.playlists[i].name === playlist) {
          self.activePlaylist.activePlaylist = self.favourites.playlists[i];
        }
      }
      localStorage.activePlaylist = angular.toJson(self.activePlaylist.activePlaylist);
      updateLocalStorageString();
    }

    function addNewPlaylist(playlist) {
      for(var i = 0 ; i < self.favourites.playlists.length ; i ++) {
        if(self.favourites.playlists[i].name === playlist) {
          return
        }
      }
      self.favourites.playlists.push({
        name: playlist,
        artists : []
      });
      localStorage.favourites = angular.toJson(self.favourites);
    }

    function makeXshowPlaylist(index) {
      $timeout(function() {
        if(angular.isUndefined(self.favourites.playlists[index])) {
            toastr.info('You have just deleted a playlist!');
        }else{
          self.favourites.playlists[index].showX = false;
            if (self.favourites.playlists[index].showXtimeout === true) {
               self.favourites.playlists[index].showX = true;
            }
          }
        }, 1000);
      self.favourites.playlists[index].showXtimeout = true;
    }

    function makeXhidePlaylist(index) {
      self.favourites.playlists[index].showXtimeout = false;
      self.favourites.playlists[index].showX = false;
    }

    function makeXshowArtist(index) {
     $timeout(function() {
      if(angular.isUndefined(self.activePlaylist.activePlaylist.artists[index])) {
        toastr.info('You have just deleted all artists from playlist!');
      }else{
        self.activePlaylist.activePlaylist.artists[index].showX = false;
        if (self.activePlaylist.activePlaylist.artists[index].showXtimeout === true) {
           self.activePlaylist.activePlaylist.artists[index].showX = true;
          }
        }
      }, 1000);
      self.activePlaylist.activePlaylist.artists[index].showXtimeout = true;
    }

    function makeXhideArtist(index) {
      self.activePlaylist.activePlaylist.artists[index].showXtimeout = false;
      self.activePlaylist.activePlaylist.artists[index].showX = false;
    }

    function removePlaylist(index) {
      var name = self.favourites.playlists[index].name;
      self.favourites.playlists.splice(index, 1);
      if(self.activePlaylist.activePlaylist.name === name) {
        self.activePlaylist.activePlaylist = {name :'no playlist created'};
        localStorage.activePlaylist = "";
      }
      localStorage.favourites = angular.toJson(self.favourites);
      updateLocalStorageString();
    }

    function removeArtist(index) {
      self.activePlaylist.activePlaylist.artists.splice(index, 1);
      localStorage.activePlaylist = angular.toJson(self.activePlaylist.activePlaylist);
      updateLocalStorageString();
    }

    function updateLocalStorageString() {
      if(localStorage.activePlaylist) {
        var activeArtistArray = [];
        var artists = angular.fromJson(localStorage.activePlaylist).artists;
        artists.forEach(function(artist) {
          if(artist.active === true) {
            activeArtistArray.push(artist.name);
          }
        });
        localStorage.artists = activeArtistArray;
      } else {
        localStorage.artists = '';
      }
    }
  }
})();


(function() {

  'use strict';

  angular
    .module('musicHead')
    .directive('favourite', favourite);

  favourite.$inject = ['YT_event','cachingFactory','toastr'];

  function favourite() {

    return {
      restrict: "E",
      templateUrl: 'app/components/NavBar/FavouriteListsFeature/favourite.template.html',
      controller: favouriteController,
      controllerAs: 'fvCtrl'
    }

    function favouriteController($timeout, dataService, YT_event, $scope, cachingFactory, toastr, favouritesService) {

      var vm = this;

      vm.playlists = favouritesService.favourites;
      vm.activePlaylist = favouritesService.activePlaylist;
      vm.toggleActive = favouritesService.toggleActive;
      vm.setActivePlaylist = setActivePlaylist;
      vm.addNewPlaylist = addNewPlaylist;
      vm.addingNewPlaylistStatus = false;
      vm.addingNewPlaylist = addingNewPlaylist;
      vm.makeXshowPlaylist = favouritesService.makeXshowPlaylist;
      vm.makeXhidePlaylist = favouritesService.makeXhidePlaylist;
      vm.makeXshowArtist = favouritesService.makeXshowArtist;
      vm.makeXhideArtist = favouritesService.makeXhideArtist;
      vm.removePlaylist = favouritesService.removePlaylist;
      vm.removeArtist = favouritesService.removeArtist;
      vm.showPlaylists = showPlaylists;
      vm.hidePlaylists = hidePlaylists;
      vm.playlistsVisible = false;
      vm.playlistsVisibleTimeout = false;
      vm.showArtists = showArtists;
      vm.hideArtists = hideArtists;
      vm.artistsVisible = false;
      vm.artistsVisibleTimeout = false;

      favouritesService.checkLocalStorage();

      function showPlaylists() {
        if (vm.artistsVisible === false) {
          vm.playlistsVisible = true;
          vm.playlistsVisibleTimeout = false;
        }
      }

      function hidePlaylists() {
        vm.playlistsVisibleTimeout = true;
        $timeout(function() {
          if (vm.playlistsVisibleTimeout) {
            vm.playlistsVisible = false;
          }
        }, 200);
      }

      function showArtists() {
        vm.artistsVisible = true;
        vm.artistsVisibleTimeout = false;
      }

      function hideArtists() {
        vm.artistsVisibleTimeout = true;
        $timeout(function() {
          if (vm.artistsVisibleTimeout) {
            vm.artistsVisible = false;
          }
        }, 200);
      }

      function setActivePlaylist(playlist) {
        vm.playlistsVisible = false;
        favouritesService.setActivePlaylist(playlist);
      }

      function addingNewPlaylist() {
        vm.addingNewPlaylistStatus = true;
        vm.newPlaylist = '';
      }

      function addNewPlaylist(playlist) {
        if (playlist) {
          favouritesService.addNewPlaylist(playlist);
        }
        $timeout(function() {
          vm.addingNewPlaylistStatus = false;
        }, 50);
      }
    }
  }
})();

