(function() {

  'use strict';

  angular
    .module('musicHead')
    .service('ytPlayerApi', ytPlayerApi);

  ytPlayerApi.$inject = ['$q','$window'];

  function ytPlayerApi($q, $window) {

    var apiReady = $q.defer();

    activate();

    this.onReady = onReady;

    function onReady() {
      return apiReady.promise;
    }

    function activate() {
      var tag = document.createElement('script');
      tag.src = "https://www.youtube.com/iframe_api";
      var firstScriptTag = document.getElementsByTagName('script')[0];
      firstScriptTag.parentNode.insertBefore(tag, firstScriptTag);

      $window.onYouTubeIframeAPIReady = function() {
        apiReady.resolve();
      }
    }
  }
})();

// Factory taht handles youtube request and cache response from server. This cache only takes objects from youtube.

(function() {

  'use strict';

  angular
    .module('musicHead')
    .factory('youtubeFactory', youtubeFactory);

  youtubeFactory.$inject = ['$http','youtubeDataService'];

  function youtubeFactory($http, youtubeDataService) {

  // Accessible members
    var cache = [],
        factory = {
          cache : [],
          showItems: showItems,
          saveCache: saveCache,
          readCache: readCache,
          clearCacheClips: clearCacheClips
    };

  // Returning object of functions

    return factory;

  // Functions declatarions

    function showItems(query) {
        var ytRequestObject = youtubeDataService.getDataObject("search", query);
        return $http({
            method: 'GET',
            url: ytRequestObject.url,
            params: ytRequestObject.params })
        .then(showItemsComplete)
        .catch(showItemsFail);

      function showItemsComplete(response) {
        cache = cache.concat(response.data.items);
        return response.data.items;
      }

      function showItemsFail(error) {
        return error.data;
      }
    }

    function saveCache(arr) {
      factory.cache = arr;
      cache = arr;
    }

    function readCache() {
        return cache;
    }

    function clearCacheClips() {
        factory.cache = [];
        cache = [];
    }
  }
})();

// Service will be returning data object that will be use for making a requests in
// youtube factory, for specific data from YT.

(function() {
	'use strict';

	angular
    .module('musicHead')
    .service('youtubeDataService', youtubeDataService);

	function youtubeDataService() {
		var vm = this;

		vm.getUrl = getUrl;
		vm.getDataObject = getDataObject;

		function getUrl(command) {
			var url = "https://content.googleapis.com/youtube/v3/";
      return command ? url + command +'?' : url;
		}

		function getDataObject(command, query) {
			var ytRequestObject = {
				url: vm.getUrl(command),
				params: {
					q: encodeURIComponent(query).replace(/%20/g, "+"),
					part: 'snippet',
					type: 'video',
					videoCategoryId: 10,
					videoDuration: 'medium',
					maxResults: 4,
					order: 'viewCount',
					key: "AIzaSyA-CgKHZFkogn14QSaYrxuCRRCvqPaQ3hI"
				}
			}
			return ytRequestObject;
		}
	}
})();

// Container which hold youtube player and controlls of that player.
// It has its own controller, to changing flag when api ready
// and to declare functionallity that we will use in youtubeplayer directive

(function() {

  'use strict';

  angular
    .module('musicHead')
    .directive('navBar', navBar);

  navBar.$inject = ['YT_event','cachingFactory','toastr'];

  function navBar() {

    return {
      restrict: "E",
      templateUrl: 'app/components/NavBar/navBar.template.html',
      controller: navBarController,
      controllerAs: 'navBarCtrl'
    }

    function navBarController($timeout, dataService, YT_event, $scope, cachingFactory, toastr) {

      var vm = this;

      vm.logOut = logOut;
      vm.pause = pause;
      vm.play = play;
      vm.stop = stop;
      vm.next = next;
      vm.previous = previous;
      vm.repeat = repeat;
      vm.setVolume = setVolume;
      vm.mute = mute;
      vm.muted = false;
      vm.volume = 100;
      vm.profilePictureUrl;
      vm.facebookLogFlag;
      vm.inputApproachFlag = cachingFactory.approachFlag;

      dataService.checkLogStatus()
        .then(setFlag)
        .then(getProfilePicture)
        .then(setProfilePictureUrl)
        .catch(function(error) {
          toastr.error(error);
        })

      function setFlag(response) {
        vm.facebookLogFlag = response.status === 'connected';
        return vm.facebookLogFlag;
      }

      function getProfilePicture(facebookLogFlag) {
        if(facebookLogFlag) {
            return dataService.getProfilePicture();
          }
      }

      function setProfilePictureUrl(picture) {
        if(picture) {
          vm.profilePictureUrl = picture.data.url;
        } else {
          return;
        }
      }

      function logOut() {
        dataService.logOutFromFb();
      }

      function pause() {
        $scope.$broadcast(YT_event.PAUSE);
      }

      function play() {
        $scope.$broadcast(YT_event.PLAY);
      }

      function stop() {
        $scope.$broadcast(YT_event.STOP);
      }

      function next() {
        $scope.$broadcast(YT_event.NEXT);
      }

      function previous() {
        $scope.$broadcast(YT_event.PREVIOUS);
      }

      function repeat() {
        $scope.$broadcast(YT_event.REPEAT);
      }

      function setVolume() {
        $scope.$broadcast(YT_event.VOLUME, vm.volume);
      }

      function mute() {
        vm.muted=!vm.muted;
        $scope.$broadcast(YT_event.MUTE);
      }
    }
  }
})();

(function() {

  'use strict';

  angular
    .module('musicHead')
    .factory('helpersFactory', helpersFactory);

  function helpersFactory() {

  // Accessible members

    var factory = {
      trimmingArray: trimmingArray,
      shuffle: shuffle,
      formatTime: formatTime
    };

  // Returning object of functions

    return factory;

  // Functions declatarions

    function trimmingArray(arr) {
      var trimmedArray = [];
      angular.forEach(arr, function(item) {
        item = item.trim();
        if (-1 === trimmedArray.indexOf(item)){
          trimmedArray.push(item);
        }
      });
      return trimmedArray;
    }

    function shuffle(array) {
      var currentIndex = array.length, temporaryValue, randomIndex;
      while (0 !== currentIndex) {
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex -= 1;
        temporaryValue = array[currentIndex];
        array[currentIndex] = array[randomIndex];
        array[randomIndex] = temporaryValue;
      }
      return array;
    }

    // Function for formating time into minutes and seconds

    function formatTime(time) {
      time = Math.round(time);
      var minutes = Math.floor(time / 60),
      seconds = time - minutes * 60;
      seconds = seconds < 10 ? '0' + seconds : seconds;
      return minutes + ":" + seconds;
    }
  }
})();

/*eslint angular/document-service: 1*/
(function() {

  'use strict';
  angular
      .module('musicHead')
      .service('FBApiService', FBApi);

  FBApi.$inject = ['$q','$location', 'cachingFactory','spinnerService'];

  function FBApi($q, $location, cachingFactory, spinnerService) {

    var self = this,
        logged = false;

    self.logIn = logIn;
    self.logOut = logOut;
    self.fbArtistsArray = [];
    self.checkStatus = checkStatus;
    self.musicRequest = musicRequest;
    self.pictureRequest = pictureRequest;

    function checkStatus() {
      var defer = $q.defer();
      FB.getLoginStatus(function(response) {
        if(response.status === 'connected') {
          defer.resolve(response);
        } else if (response.status === 'unknown') {
          defer.resolve(response);
        } else {
          defer.reject('You are logged out from Facebook!');
        }
      });
      return defer.promise;
    }

    function logIn() {
      var defer = $q.defer();
      FB.login(function(response) {
        if(response.authResponse) {
          logged = true;
          cachingFactory.cacheFacebookLogFlag(logged);
          pictureRequest()
            .then(function(picture) {
              cachingFactory.cacheFacebookProfilePicture(picture);
            })
          musicRequest()
            .then(function(fbArtistsArray) {
              defer.resolve(fbArtistsArray);
            });
        } else {
          defer.reject('Facebook has a problem with login function');
        }
      }, {scope: 'user_likes'} );
      return defer.promise;
    }

    function logOut() {
      spinnerService.show('mySpinner');
      var defer = $q.defer();
      FB.getLoginStatus(function(response) {
        if (response.status === 'connected') {
          FB.logout(function() {
            defer.resolve();
          });
        } else {
          defer.reject('Facebook can\'t get your login status');
        }
      });
      return defer.promise;
    }

    function musicRequest() {
      var defer = $q.defer();
        FB.api('/me/music', 'GET', {}, function(response) {
          if (!response || response.error) {
            defer.reject(response.error.data)
          } else {
            self.fbArtistsArray = [];
            angular.forEach(response.data, function(value) {
              self.fbArtistsArray.push(value.name)
            })
            cachingFactory.clearCachedUrlId();
            cachingFactory.clearCachedArray();
            cachingFactory.cacheArray(self.fbArtistsArray);
            defer.resolve(self.fbArtistsArray);
          }
        });
        return defer.promise;
    }

    function pictureRequest() {
      var defer = $q.defer();
      FB.api('/me/picture', 'GET', {}, function(picture) {
        if (!picture || picture.error) {
          defer.reject(picture.error.data)
        } else {
          defer.resolve(picture);
        }
      });
      return defer.promise;
    }
  }
})();


