(function() {
  'use strict';

  angular
    .module('musicHead')
    .controller('FacebookController', fbCtrl);

  fbCtrl.$inject = ['FBApiService','$location','dataService','spinnerService','toastr'];

  function fbCtrl(FBApiService, $location, dataService, spinnerService, toastr) {

    var vm = this;

    vm.getResource = getResource;

    function getResource() {

      spinnerService.show('mySpinner');

      vm.logIn = logIn;
      vm.facebookLogFlag = false;
      vm.logOut = logOut;
      vm.changePath = changePath;

      dataService.checkLogStatus()
        .then(setFlag)
        .then(function() {
          spinnerService.hide('mySpinner');
        })
        .catch(function(error) {
          toastr.error(error);
          spinnerService.hide('mySpinner');
        })

      function logIn() {
        FBApiService.logIn()
        .then(vm.changePath)
        .catch(function(error) {
          toastr.error(error);
        })
      }

      function logOut() {
        dataService.logOutFromFb();
        vm.facebookLogFlag = false;
      }

      function changePath() {
        $location.path("/allArtists");
      }

      function setFlag(response) {
        vm.facebookLogFlag = response.status === 'connected';
      }
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('musicHead')
    .controller('MainController', MainController);

  MainController.$inject = ['$scope','dataService','cachingFactory','helpersFactory', 'youtubeFactory', '$routeParams','$location','spinnerService','toastr', 'favouritesService'];

  function MainController($scope, dataService, cachingFactory, helpersFactory, youtubeFactory, $routeParams, $location, spinnerService, toastr, favouritesService) {

    var vm = this,
        inputApproach = cachingFactory.readInputApprachFlag();

    vm.getSpinner = getSpinner;

    function getSpinner() {

      spinnerService.show('mySpinner');

      vm.artistsArrayTrimmed;
      vm.favourites = [];
      vm.clips;
      vm.videoId;
      vm.facebookLogFlag;
      vm.changeVideo = changeVideo;
      vm.routeArtist = $routeParams.artist;
      vm.keepPage = keepPage;
      vm.addToFavourites = addToFavourites;
      vm.inputApproachFlag = cachingFactory.approachFlag;
      vm.currentPage;
      vm.pageSize = 9;
      vm.currentClipTitle;

      init();
    }

    function init() {
      dataService
        .checkLogStatus()
        .then(function(response) {
          if (response.status !== 'connected' && !inputApproach) {
            $location.path("/");
            return;
          }

          vm.facebookLogFlag = response.status === 'connected'; // setup flag for showing button in view. true if connected
          var connected = response.status === 'connected'; // flag for getArtists function

          dataService.getArtists(connected)
            .then(trimArtists)
            .then(prepareArtistsArray)
            .then(prepareDataForDisplayingClips)
            .then(function() {
              spinnerService.hide('mySpinner');
            }).catch(function(error) {
              toastr.error(error);
              spinnerService.hide('mySpinner');
            })
        });
        favouritesService.checkLocalStorage();
    }

    function trimArtists(artists) {
      vm.artistsArrayTrimmed = artists;
      return artists;
    }

    function prepareArtistsArray(artists) {
      var artistsArray;
        if (vm.routeArtist) {
          artistsArray = [vm.routeArtist];
        } else {
          artistsArray = artists;
        }
        return dataService.getMusicVideosFromYt(artistsArray);
    }

    function prepareDataForDisplayingClips(artistsClips) {
      vm.currentPage = cachingFactory.readCurrentPaginationPage();
        if(artistsClips.length > 1 || artistsClips[0].length) {
          var objOfClipsAndId = dataService.getVideosAndPlayId(artistsClips);
          vm.clips = objOfClipsAndId.clips;
          vm.videoId = objOfClipsAndId.id;
          vm.currentClipTitle = vm.clips[0].snippet.title;
        } else {
          dataService.getVideosAndPlayId(artistsClips);
        }
    }

    function changeVideo(video) {
      vm.videoId = video.id.videoId;
    }

    function keepPage() {
      cachingFactory.saveCurrentPaginationPage(vm.currentPage);
    }

    function addToFavourites (artist) {
      favouritesService.addToFavourites(artist);
    }

    $scope.$on('newId', function(evt, newid) {
      var currentClip = vm.clips.filter(function(clip) {
        return clip.id.videoId === newid;
      })
      vm.currentClipTitle = currentClip[0].snippet.title;
    });
  }
})();


(function() {

  'use strict';

  angular
    .module('musicHead')
    .directive('appInput', appInput);

  appInput.$inject = ['youtubeFactory','cachingFactory','$location','helpersFactory','FBApiService','dataService','toastr'];

  function appInput(youtubeFactory, cachingFactory, $location, helpersFactory, FBApiService, dataService,toastr) {

    return {
      restrict: "E",
      templateUrl: 'app/StartPage/input.template.html',
      controller: startController,
      controllerAs: 'startCtrl'
    }

    function startController() {
      var vm = this,
          inputApproach = false;

      vm.artistsList;
      vm.getArtists = getArtists;

      function getArtists() {
        inputApproach = true;
        dataService.checkLogStatus()
          .then(function(response) {
            if(response.status === 'connected') {
              FBApiService.logOut()
                .then(prepareApp)
                .catch(function(error) {
                  toastr.error(error);
                })
            } else {
              prepareApp();
            }
          });
      }

      function prepareApp() {
        cachingFactory.clearCachedUrlId();
        youtubeFactory.clearCacheClips();
        if (localStorage.artists) { // get artists from favourites
          vm.artistsList = vm.artistsList + ',' + localStorage.artists;
        }
        if (!vm.artistsList) {return}
        vm.artistsList = vm.artistsList.split(",");
        vm.artistsList = helpersFactory.trimmingArray(vm.artistsList);
        if (vm.artistsList[0] === 'undefined' || vm.artistsList[0] === '') {
          vm.artistsList.splice(0,1);
        }
        cachingFactory.cacheArray(vm.artistsList);
        cachingFactory.cacheInputApprachFlag(inputApproach);
        vm.artistsList = ''; // clear input
        $location.path("/allArtists");
      }
    }
  }
})();

// Factory that store functions and data for displaying some info in my app.
// It caches input with artists, and id video that have to be play. Also clear functions are included.

(function() {

	'use strict';

	angular
		.module('musicHead')
		.factory('cachingFactory', cachingFactory);

	function cachingFactory() {

	// Accessible members
		var
    cacheInputArray = [],
    cachedUrlId,
    facebookLogFlag,
    inputApproachFlag,
    currentPage=1,
    facebookProfilePicture = {};

		var factory = {
      approachFlag: {approachFlag:''},
			cacheArray: cacheArray,
			readInputArrayFromCache: readInputArrayFromCache,
			cacheUrlId: cacheUrlId,
			readCacheUrlId: readCacheUrlId,
      clearCachedUrlId: clearCachedUrlId,
      clearCachedArray: clearCachedArray,
      cacheFacebookLogFlag: cacheFacebookLogFlag,
      readFacebookLogFlag: readFacebookLogFlag,
      cacheInputApprachFlag:cacheInputApprachFlag,
      readInputApprachFlag: readInputApprachFlag,
      saveCurrentPaginationPage: saveCurrentPaginationPage,
      readCurrentPaginationPage: readCurrentPaginationPage,
      cacheFacebookProfilePicture: cacheFacebookProfilePicture,
      readFacebookProfilePicture: readFacebookProfilePicture

		};

	// Returning object of functions

		return factory;

	// Functions declatarions

		function cacheArray(arr) {
			cacheInputArray = arr;
		}

		function readInputArrayFromCache() {
			return cacheInputArray;
		}

		function cacheUrlId(id) {
			cachedUrlId = id;
		}

		function readCacheUrlId() {
			return cachedUrlId;
		}

    function cacheFacebookLogFlag(flag) {
      facebookLogFlag = flag;
      factory.approachFlag.approachFlag = false;
    }

    function readFacebookLogFlag() {
      return facebookLogFlag;
    }

    function clearCachedUrlId() {
      cachedUrlId = undefined;
    }

    function clearCachedArray() {
      cacheInputArray = [];
    }

    function cacheInputApprachFlag(flag) {
      inputApproachFlag = flag;
      factory.approachFlag.approachFlag = flag;
    }

    function readInputApprachFlag() {
      return inputApproachFlag;
    }

    function saveCurrentPaginationPage(page) {
      currentPage = page;
    }

    function readCurrentPaginationPage() {
      return currentPage
    }

    function cacheFacebookProfilePicture(picObj) {
      facebookProfilePicture = picObj;
    }

    function readFacebookProfilePicture() {
      return facebookProfilePicture
    }
	}
})();

